.App {
  position: relative;
  height: 100vh;
  width: 100%;
  /* background: linear-gradient(132.96deg, #333333 15.95%, #D9D6D6 145.72%); */
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}

.Header {
  position: absolute;
  width: 60vw;
  height: 107px;
  left: 27vw;
  top: 33vh;
  font-family: Poppins;
  font-style: normal;
  font-weight: bold;
  font-size: 60px;
  line-height: 105px;
  display: flex;
  align-items: center;
  color: #D1D1D1;
}


.input-search {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 12px 16px;

  position: absolute;
  width: 555px;
  height: 48px;
  left: 520px;
  top: 475px;


  font-size: 20px;
  /* background: #D3CCE3;  fallback for old browsers */
  /* background: -webkit-linear-gradient(to right, #E9E4F0, #D3CCE3);  Chrome 10-25, Safari 5.1-6 */
  /* background: linear-gradient(to right, #E9E4F0, #D3CCE3); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  box-sizing: border-box;
  border-radius: 10px;
}

::-webkit-input-placeholder {
  color: #ffffff, 100%;
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 150%;
}

:-ms-input-placeholder {
  color: #ffffff, 100%;
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 150%;
}

::placeholder {
  color: #ffffff, 100%;
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 150%;
}

.button-search {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px 24px;

  position: absolute;
  width: 138px;
  height: 48px;
  left: 1090px;
  top: 475px;

  border: 2px solid #333333;
  box-sizing: border-box;
  border-radius: 10px;

  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 150%;
  color: #FFFFFF;

  transition: all 0.15s ease-in-out;

  background-image: linear-gradient(to right, #FF512F 0%, #DD2476  51%, #FF512F  100%);
}

.button-search:hover {
  background-position: right center; /* change the direction of the change here */
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
  color: #fff;
  text-decoration: none;
}



.avatar-home {
border: 2px solid #000000;
box-sizing: border-box;
border-radius: 100%;
margin: 15px;
width: 120px;
}

.user-card {
  margin: 20px;
  display: flex;
  flex-direction: column;
  text-align: center;
  transition: all 0.10s ease-in-out;
}

.user-card:hover {
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

.users-cards {
  display: flex;
  justify-content: center;
  position: absolute;
  width: 1080px;
  height: 240px;
  left: 365px;
  top: 580px;
}

.user-link {
  font-size: 24px;
  color: #D1D1D1;
  text-decoration: none;
  align-items: center;
}

.repo-card {
  border: 2px solid #D1D1D1;
  margin: 15px;
  padding: 0 10px;
  width: 20vw;
}

.user-repo {
  font-size: 24px;
  color: #D1D1D1;
}

.user-repo-data {
  font-size: 18px;
  color: #D1D1D1;
}

.repo-content {
  font-family: Poppins;
}

.all-repo-cards {
  display: flex;
}

