
.avatar-home {
border: 2px solid #000000;
box-sizing: border-box;
border-radius: 100%;
margin: 15px;
width: 120px;
}

.user-card {
  margin: 20px;
  display: flex;
  flex-direction: column;
  text-align: center;
  transition: all 0.10s ease-in-out;
}

.user-card:hover {
  transform: scale(1.1, 1.1);
}

.users-cards {
  display: flex;
  justify-content: center;
  position: absolute;
  width: 1080px;
  height: 240px;
  left: 365px;
  top: 580px;
}

.user-link {
  font-size: 24px;
  color: #D1D1D1;
  text-decoration: none;
  align-items: center;
}

.repo-card {
  border: 2px solid #D1D1D1;
  margin: 15px;
  padding: 0 10px;
  width: 20vw;
}

.user-repo {
  font-size: 24px;
  color: #D1D1D1;
}

.user-repo-data {
  font-size: 18px;
  color: #D1D1D1;
}

.repo-content {
  font-family: Poppins;
}

.all-repo-cards {
  display: flex;
}
